<template>
  <div class="sections">
    <div class="pl_body">
      <Tabs>
        <TabPane label="信美代偿" name="xinmei">
          <Row class="form_row">
            <Col :sm="22" :xs="24">
                <Form :model="formSel" class="downMenu">
                    <FormItem>
                        <Input v-model="formSel.input" icon="ios-search" placeholder="借款编号/客户姓名/手机号" @on-enter=""></Input>
                        <Button type="primary" @click="">查询</Button>
                        <DatePicker :value="formSel.selectTm" format="yyyy/MM/dd" type="daterange" placement="bottom-start" placeholder="借款时间" @on-change="dateChange"></DatePicker>
                        <DatePicker :value="formSel.repayTm" format="yyyy/MM/dd" type="daterange" placement="bottom-start" placeholder="应还时间" @on-change="dateChange1"></DatePicker>
                        <!--<Dropdown class="showDate" trigger="custom" :visible="vs">
                            <Button type="ghost" :class="{active:vs,chk:formSel.overdue!=''}" @click="vs=true">
                                逾期天数<span v-text="formSel.overdue"></span>
                                <Icon type="arrow-down-b"></Icon>
                            </Button>
                            <DropdownMenu slot="list">
                                <FormItem class="itemNum">
                                    <Row>
                                        <Col :sm="11" :xs="20">
                                            <InputNumber :min="1" v-model="formSel.overdueVal" placeholder="开始天数"></InputNumber>
                                        </Col>
                                        <Col :sm="2" :xs="4" style="text-align: center">-</Col>
                                        <Col :sm="11" :xs="24">
                                            <InputNumber :min="1" v-model="formSel.overdueVal1" placeholder="结束天数"></InputNumber>
                                        </Col>
                                    </Row>
                                </FormItem>
                                <Row class="btn_click" style="float:right;">
                                    <Col span="12">
                                        <Button type="ghost" @click="vs=!vs">取消</Button>
                                    </Col>
                                    <Col span="12">
                                        <Button type="primary" @click="overdueClick">确定</Button>
                                    </Col>
                                </Row>
                            </DropdownMenu>
                        </Dropdown>-->
                        <Dropdown trigger="click" class="showMn" @on-click="showMenu2" @on-visible-change="orShow2">
                            <Button type="ghost" :class="{active:vs2,chk:formSel.typeBs!=''}">
                                业务类型<span v-text="formSel.typeBs"></span>
                                <Icon type="arrow-down-b"></Icon>
                            </Button>
                            <DropdownMenu slot="list">
                                <DropdownItem name="全部" :selected="formSel.typeBs==''">全部</DropdownItem>
                                <DropdownItem name="现金贷" :selected="formSel.typeBs=='0'">现金贷</DropdownItem>
                                <DropdownItem name="消费贷" :selected="formSel.typeBs=='1'">消费贷</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                        <!--<Dropdown trigger="click" class="showMn" @on-click="showMenu3" @on-visible-change="orShow3">
                            <Button type="ghost" :class="{active:vs3,chk:formSel.channelBs!=''}">
                                业务渠道<span v-text="formSel.channelBs"></span>
                                <Icon type="arrow-down-b"></Icon>
                            </Button>
                            <DropdownMenu slot="list">
                                <DropdownItem name="全部" :selected="formSel.channelBs=='全部'">全部</DropdownItem>
                                <DropdownItem name="普通" :selected="formSel.channelBs=='普通'">普通</DropdownItem>
                                <DropdownItem name="安利" :selected="formSel.channelBs=='安利'">安利</DropdownItem>
                                <DropdownItem name="华人" :selected="formSel.channelBs=='华人'">华人</DropdownItem>
                                <DropdownItem name="国美" :selected="formSel.channelBs=='国美'">国美</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>-->
                        <!--<Dropdown trigger="click" class="showMn" @on-click="showMenu4" @on-visible-change="orShow4">-->
                            <!--<Button type="ghost" :class="{active:vs4,chk:formSel.typePd!=''}">-->
                                <!--产品类型<span v-text="formSel.typePd"></span>-->
                                <!--<Icon type="arrow-down-b"></Icon>-->
                            <!--</Button>-->
                            <!--<DropdownMenu slot="list">-->
                                <!--<DropdownItem name="全部" :selected="formSel.typePd=='全部'">全部</DropdownItem>-->
                                <!--<DropdownItem name="消费分期" :selected="formSel.typePd=='消费分期'">消费分期</DropdownItem>-->
                                <!--<DropdownItem name="数码3C分期" :selected="formSel.typePd=='数码3C分期'">数码3C分期</DropdownItem>-->
                                <!--<DropdownItem name="小美钱包" :selected="formSel.typePd=='小美钱包'">小美钱包</DropdownItem>-->
                                <!--<DropdownItem name="美容分期" :selected="formSel.typePd=='美容分期'">美容分期</DropdownItem>-->
                            <!--</DropdownMenu>-->
                        <!--</Dropdown>-->
                        <Dropdown trigger="click" class="showMn" @on-click="showMenu5" @on-visible-change="orShow5">
                            <Button type="ghost" :class="{active:vs5,chk:formSel.moneyGv!=''}">
                                资金提供方<span v-text="formSel.moneyGv"></span>
                                <Icon type="arrow-down-b"></Icon>
                            </Button>
                            <DropdownMenu slot="list">
                                <DropdownItem name="全部" :selected="formSel.moneyGv=='0'">全部</DropdownItem>
                                <DropdownItem name="华金" :selected="formSel.moneyGv=='1'">华金</DropdownItem>
                                <DropdownItem name="中科诺" :selected="formSel.moneyGv=='2'">中科诺</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                    </FormItem>
                </Form>
            </Col>
            <Col :sm="2" :xs="24">
              <div class="float_right">
                <Button type="success" @click="cps(true)"> 信美代偿</Button>
              </div>
            </Col>
        </Row>
        <div class="table_show">
        <Table border :columns="columns1" :data="data1" ref="table"></Table></div>
        <Row :gutter="16" v-if="pageWin>=768">
            <Col :sm="5" :xs="24">
                <div class="total">共{{total}}页</div>
            </Col>
            <Col :sm="19" :xs="24" class="txt_right">
                <Page :total="total" show-elevator @on-change="changePage"></Page>
            </Col>
        </Row>
        <Page v-else :total="total" simple @on-change="changePage"></Page>
        </TabPane>
        <TabPane label="代理商代偿" name="agent">
          <Form :model="formSel" class="downMenu">
            <FormItem>
              <Input v-model="formSel.input" icon="ios-search" placeholder="借款编号/客户姓名/手机号" @on-enter=""></Input>
              <Button type="primary" @click="">查询</Button>
            </FormItem>
          </Form>
          <div class="table_show">
          <Table border :columns="columns2" :data="data2" ref="table"></Table></div>
          <Row :gutter="16" v-if="pageWin>=768">
              <Col :sm="5" :xs="24">
                  <div class="total">共{{total}}页</div>
              </Col>
              <Col :sm="19" :xs="24" class="txt_right">
                  <Page :total="total" show-elevator @on-change="changePage"></Page>
              </Col>
          </Row>
          <Page v-else :total="total" simple @on-change="changePage"></Page>
        </TabPane>
      </Tabs>
        
    </div>

    <!--代偿确认-->
    <Modal v-model="cpsModal" title="信美代偿确认">
      <div slot="footer">
        <Button type="text" size="large" @click="cpsModal=!cpsModal">取消</Button>
        <Button type="primary" size="large" @click="cpsConfirm">确定</Button>
      </div>
      <Form :label-width="60">
        <FormItem label="时间"><DatePicker :value="selectTm" format="yyyy-MM-dd" placement="bottom-start" @on-change="cpsTmChange" placeholder="偿还时间"></DatePicker></FormItem>
        <!--<FormItem label="偿还金额"><Input v-model="mn" placeholder="偿还金额"></Input></FormItem>-->
        <FormItem label="偿还流水号"><Input v-model="sn" placeholder="偿还流水号"></Input></FormItem>
      </Form>
    </Modal>

    <Modal v-model="cpsModal2" title="信美代偿">
      <Form :model="compenstateForm">
        <FormItem>
          <DatePicker type="month" :value="compenstateForm.selectTm" format="yyyy-MM" placement="bottom-start" @on-change="monthChange" placeholder="代偿月份" :options="options1"></DatePicker>
        </FormItem>
        <FormItem>
          <Input v-model="compenstateForm.money" placeholder=" 输入代偿金额"></Input>
        </FormItem>
      </Form>
    </Modal>

  </div>
</template>

<script>

  import axios from 'axios';

export default {
  name: 'sections',
  data () {
    return {
        vs:false,
        vs2: false,
        vs3: false,
        vs4: false,
        vs5: false,
        cpsModal: false,
        cpsModal2: false,
        tbWidth:100,
        total:100,
        selectTm: '',
        sn: '',
        mn: '',
        tabVal:'xinmei',
        formSel:{
            overdueVal:1,
            overdueVal1:1,
            input:'',
            overdue:'',
            selectTm:'',
            repayTm:'',
            typeBs:'',
            channelBs:'',
            typePd:'',
            moneyGv:''
        },
        money:'',
        compenstateForm:{
          selectTm:'',
          money:''
        },
        columns1: [{
            type:'selection',
            width:60,
            align:'center'
          },
          {
            title: '借款编号',
            key: 'loanNo'
          },{
            title: '客户姓名',
            key: 'custName'
          },{
            title: '手机号',
            key: 'custMobile'
          },{
            title: '身份证号',
            key: 'idcard'
          },
          {
            title: '借款金额',
            key: 'loanAmount'
          },
          {
            title: '首付金额',
            key: 'downpayAmount'
          },
          {
            title: '借款时间',
            key: 'loanTime',
            width:104
          },
          {
            title: '省份',
            key: 'province'
          },
          {
            title: '城市',
            key: 'city'
          },
          {
            title: '总期数',
            key: 'totalStage'
          },
          {
            title: '当前期数',
            key: 'planStage'
          },
          {
            title: '应还时间',
            key: 'planLtm',
            width:104
          },
          {
            title: '应还本金',
            key: 'planPrp'
          },
          {
            title: '应还服务费',
            key: 'planSfe'
          },
          {
            title: '应还利息',
            key: 'planInt'
          },
          {
            title: '应还罚息',
            key: 'planPin'
          },
          {
            title: '应还违约金',
            key: 'd'
          },
          {
            title: '应还总金额',
            key: 'planTotal'
          },
          {
            title: '逾期天数',
            key: 'exceedDay'
          },
          {
            title: '代偿状态',
            key: 'cpsSt'
          },
          {
            title: '备注',
            key: 'clrType'
          }],
        data1: [],
        columns2:[{
          title:'代理商名称',
          key:'agentName'
        },
        {
          title:'法人姓名',
          key:'name'
        },
        {
          title:'手机号',
          key:'phone'
        },
        {
          title:'省市',
          key:'address'
        },{
          title:'操作',
          key:'action',
          width:80,
          align:'center',
          render:(h,params)=>{
            return h('div',[
              h('Button',{
                props:{
                  type:'primary',
                  size:'small'
                },
                on:{
                  click:() =>{
                    this.cpsModal2=true;
                  }
                }
              },'代偿')
            ])
          }
        }],
        data2:[{
          agentName:'深圳高明有限公司',
          name:'张一河',
          phone:'13655556666',
          address:'广东省深圳市'
        }],
        options1:{
          disabledDate(date){
            var now = new Date();
            var current;
            if (now.getMonth() == 0) {
                current = new Date(now.getFullYear() - 1, 11, 1);
            } else {
                current = new Date(now.getFullYear(), now.getMonth() - 1, 1);
            }
            
            var lastDay = new Date(current.getFullYear(), current.getMonth() + 1, 0);
            return date && date.valueOf()>=(new Date(current.getFullYear()+"-"+(current.getMonth()+1)+"-"+lastDay.getDate())-86400000);
            //return date && date.valueOf()>(new Date("2017-12-31"));
            //return date && date.valueOf()<(Date.now()-86400000);
          }
        }
        }
    },
    created:function(){
        if($(window).width()<768){
            var arr=this.columns1;
            for(var i=0;i<arr.length;i++){
                arr[i]['width']=this.tbWidth;
            }
            this.columns1=arr;
        }
        var now = new Date();
        var current;
        if (now.getMonth() == 0) {
            current = new Date(now.getFullYear() - 1, 11, 1);
        } else {
            current = new Date(now.getFullYear(), now.getMonth() - 1, 1);
        }
        this.compenstateForm.selectTm = (current.getFullYear()).toString() + '-' +(current.getMonth()+1).toString()//'2017-12'
    },
    computed :{
        pageWin(){
            return Math.max(window.innerWidth,document.documentElement.clientWidth,document.body.clientWidth);
        }
    },
    mounted(){
      this.loadContent(1)
    },
    methods: {
        cpsTmChange(cpsTm){
          this.selectTm = cpsTm
        },
        monthChange(cpsTm){
          this.compenstateForm.selectTm = cpsTm
        },
        cps(isShow){
          var cpsData = this.$refs.table.getSelection()
          console.log(cpsData)
          if(cpsData.length<=0){
            this.$Message.error('代偿计划不能为空')
            return
          }

          this.cpsModal = isShow
        },
        cpsConfirm(){

          var self = this

          var cpsData = this.$refs.table.getSelection()
          console.log(cpsData)
          if(cpsData.length<=0){
            this.$Message.error('代偿计划不能为空')
            return
          }

          var planIds = []

          for (var i=0; i< cpsData.length; i++){
            var item = cpsData[i]
            console.log("item planId", item.planId)
            planIds.push(item.planId)
          }

          console.log(this.selectTm, this.mn, this.sn, planIds)

          if(this.selectTm == null || this.selectTm == ''){
            this.$Message.error('代偿时间为空')
            return
          }
//          if(this.mn == null || this.mn == ''){
//            this.$Message.error('代偿金额不能为空')
//            return
//          }

          $.ajax({
            url : "/compensatory/cpsConfirm",
            type : "post",
            cache : false,
            dataType : "json",
            xhrFields: {
              withCredentials: true
            },
            data : {
              cpsTm : this.selectTm,//待代偿
              cpsAmt :this.mn,
              cpsSn :this.sn,
              planIds : JSON.stringify(planIds)
            },
            contentType : "application/x-www-form-urlencoded; charset=utf-8",

            success : function(data) {
              //{"msg":"OK","resCode":1,"result":"操作成功","success":true}
              console.log(JSON.stringify(data))
              if(data.success && data.resCode == 1){
                self.$Message.error('操作成功')
                self.loadContent(1)
                self.cpsModal = false
              }
            }
          });

        },
        showMenu2(val){
            if(val!='全部' && val!=''){
                this.formSel.typeBs=' : '+val;
            }else{
                this.formSel.typeBs='';
            }
        },
        orShow2(visible){
            this.vs2=visible;
        },
        showMenu3(val){
            if(val!='全部' && val!=''){
                this.formSel.channelBs=' : '+val;
            }else{
                this.formSel.channelBs='';
            }
        },
        orShow3(visible){
            this.vs3=visible;
        },
        showMenu4(val){
            if(val!='全部' && val!=''){
                this.formSel.typePd=' : '+val;
            }else{
                this.formSel.typePd='';
            }
        },
        orShow4(visible){
            this.vs4=visible;
        },
        showMenu5(val){
            if(val!='全部' && val!=''){
                this.formSel.moneyGv=' : '+val;
            }else{
                this.formSel.moneyGv='';
            }
        },
        loadContent(page){
          var firstResult = (parseInt(page) - 1) * 10;
          var pageSize = 10;
          var that=this;

          $.ajax({
            url : "/compensatory/queryAllCompensatoryInfos",
            type : "post",
            cache : false,
            dataType : "json",
            xhrFields: {
              withCredentials: true
            },
            data : {
              cpsSt : 1,//待代偿
              pageStart :firstResult,
              pageSize :pageSize
            },
            contentType : "application/x-www-form-urlencoded; charset=utf-8",

            success : function(data) {

              console.log(JSON.stringify(data))
              that.total = data.count

              if(data.resCode == 1){
                that.total = data.count.count;
                data.result.forEach(function(element) {
                  if (element.cpsSt == 0){
                    element.cpsSt = '未到代偿日'
                  }else if(element.cpsSt == 1){
                    element.cpsSt = '待代偿'
                  }else if(element.status == 2){
                    element.cpsSt = '代偿成功'
                  }
                }, this);
                that.data1 = data.result;
              }
            }
          });
        },
        orShow5(visible){
            this.vs5=visible;
        },
        dateChange(date){
            this.formSel.selectTm=date;
        },
        dateChange1(date){
            this.formSel.repayTm=date;
        },
        overdueClick(){
            this.formSel.overdue=' : '+this.formSel.overdueVal+'-'+this.formSel.overdueVal1;
            this.vs=false;
        },
        exportData () {
            this.$Modal.confirm({
                title:'信美代偿',
                render:(h) => {
                    return h('Input',{
                        props:{
                            value:this.money,
                            autofocus:true,
                            placeholder:'输入代偿金额'
                        },
                        style:{
                            marginTop:'20px'
                        },
                        on:{
                            input:(val) =>{
                                this.money=val;
                            }
                        }
                    })
                }
            })
        },
        changePage(){
            /*this.data1=this.*/
        }
    }
}
</script>



